
import { Options, Vue } from 'vue-class-component';
import Line from '@/components/Line.vue'

@Options({
  components: {
    'a-line': Line
  }
})
export default class ResumeInfo extends Vue {
  private show: boolean = false;
  private userInfo: any = {
    name: '益立锋',
    photo: require('@/assets/static/img/photo.jpg'),
    collage: '毕业于西安理工大学，计算机科学与技术专业',
    workInfo: '工作6年, 服务器端开发2年, web开发4年'
  };
  private states: any = [
    {icon: 'icon-nianling', val: '年龄/三十'},
    {icon: 'icon-xueli', val: '学历/本科'},
    {icon: 'icon-icon-', val: '坐标/西安'},
    {icon: 'icon-zhiwei', val: '状态/在职'}
  ];

  timeout () {
    setTimeout(() => {
      this.show = true
    }, 1000);
  }

  mounted () {
    this.timeout();
  }
}
