
import { Options, Vue } from 'vue-class-component';
import Con1 from '@/views/resume_info.vue'
import Con2 from '@/views/resume_knowlege.vue'
import Con3 from '@/views/resume_works.vue'
import Con4 from '@/views/resume_connect.vue'

@Options({
  components: {
    Con1,
    Con2,
    Con3,
    Con4
  }
})

export default class About extends Vue {
  private name: string = 'About';
  private img: string = require('@/assets/static/img/bg1.jpg');
  private clientHeight: string = `${document.body.offsetHeight}px`;
  private init: number = 1;
  private lastscroll: number = 0;
  private leaveanimate: string = 'zoomOutUp';
  private enteranimate: string = 'zoomInDown';
 
  // computed
  get MyName(): string {
    return `My name is ${this.name}`;
  }

  mouse (event: any) {
    // 防止用户短时间内滚动多次，设置滚动间隔大于一秒才能生效
    // 判断滚动间隔时间
    const scrollduration = event.timeStamp - this.lastscroll
    console.log(scrollduration)
    if (scrollduration > 1000) {
      // 将这一次的滚动时间记录为上一次合法的滚动时间
      this.lastscroll = event.timeStamp
      console.log('合法的滚动')
      // 判断滚动方向进行操作
      if (event.deltaY > 0) {
        console.log('down')
        if (this.init === 4) {
          this.init = 1
        } else {
          this.init = this.init + 1
        }
      } else {
        console.log('up')
        if (this.init === 1) {
          this.init = 4
        } else {
          this.init = this.init - 1
        }
      }
    } else {
      // 如果滚动不合法就不做任何操作
      console.log('请爱护你的鼠标不要连续滚动！')
    }
  }

  nextPage () {
    if (this.init === 4) {
      this.init = 1
    } else {
      this.init = this.init + 1
    }
  }
 
  // methods
  mounted() {
    this.clientHeight = `${document.documentElement.clientHeight}px`;
    // 然后监听window的resize事件．在浏览器窗口变化时再设置下背景图高度
    window.onresize = () => {
      this.clientHeight = `${document.documentElement.clientHeight}px`;
      console.log('height', this.clientHeight);
    }
  }
}
